import React from 'react';
import styled from 'styled-components';

const Section = styled.div`
  height: 100vh;  
  scroll-snap-align: center;
  display: flex;
  justify-content: center;
`
const Container = styled.div`
  width: 1200px;
  display: flex;
  justify-content: space-between;
`
const Left = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  
`
const List = styled.ul`
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 10px;
`
const ListItems = styled.li`
  font-size: 50px;
  font-weight: bold;
  cursor: pointer;
  color: transparent;
  -webkit-text-stroke: 1px white;
  position: relative;
  ::after{
    content: "${(props)=>props.text}";
    position: absolute;
    top: 0;
    left: 0;
  }

  &:hover{
    ::after{
      animation: moveText 0.5s linear;
    }
  }
`




const Right = styled.div`
  flex: 1;
`

const data = [
  "React TypeScript",
  "Redux Toolkit",
  "Hooks",
  "Node.js",
];


const Project = () => {
  return (
    <Section>
      <Container>
        <Left>
          <List>
            {data.map((item) => (
              <ListItems key={item} text={item}>{item}</ListItems>
            ))}
            
          </List>
        </Left>
        <Right>

        </Right>
      </Container>
    </Section>
  )
}

export default Project